  gap: 33px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 500;
  line-height: 20px;
}
@media (max-width: 1023px) {
  .footer-nav__list {
    margin: 3px 0 0;
    gap: 23px;
    font-size: 12px;
    line-height: 15px;
  }
}
@media (max-width: 767px) {
  .footer-nav__list {
    margin: 0;
    flex-direction: column;
    gap: 17px;
  }
}

.footer-nav__link {
  color: #f9fbfd;
}

.footer__socials {
  margin: 10px 1px 0 auto;
}
@media (max-width: 1023px) {
  .footer__socials {
    margin: 3px 0 0 auto;
  }
}
@media (max-width: 767px) {
  .footer__socials {
    margin: 0;
  }
}

